import React, { Component } from 'react';
import logo from './logo.svg';
import './App.scss';
import NavList from './components/NavList/index.js';
import Header from './components/Header/index.js';
import Frame from './components/Frame/index.js';

let g=window.webqConfig?window.webqConfig:[];

class App extends Component {
  constructor(props){
    super(props);
    this.list=g?g:[];
    this.state={
      focus:g[0]?g[0]:{}
    }
  }
  changeIframe=(index)=>{
    this.setState({
        focus:this.list[index]
    });
  }
  render() {
    return (
      <div className="App">

        <Header/>
        <div className="layout">
           <div className="layout-left" ><NavList list={this.list} focusTrigger={this.changeIframe}/></div>
           <div className="layout-center">

            <Frame data={this.state.focus}/>
           </div>
              </div>
      </div>
    );
  }
}

export default App;



