import {ListGroup,ListGroupItem} from 'react-bootstrap';
import React, { Component } from 'react';
import './index.scss';

class NavList extends Component {
  constructor(props){
    super(props);
    this.last=null;
  }
  render() {
    let link=this.props.data.link;
    let last=this.last;
    console.log(link,last);
    return (
      <div className="frame">
        <a type="button" class="btn btn-info linkto" target="_blank" href={this.props.data.link}>{this.props.data.name} link button(if iframe can not show)</a>

        <iframe src={this.props.data.link} frameBorder="0" allowFullScreen="true"></iframe>
      </div>
    );
  }
}

export default NavList;



