import {ListGroup,ListGroupItem} from 'react-bootstrap';
import React, { Component } from 'react';
import './index.css';
import classnames from 'classnames';

class NavList extends Component {
  constructor(props){
    super(props);
    this.focus=0;
    this.state={
      focus:0
    }
  }
  setFocus=(focus)=>{
    this.setState({
      focus
    });
    let {focusTrigger}=this.props;
    if(typeof focusTrigger==='function'){
      focusTrigger(focus);
    }
  }
  render() {

    return (
      <div className="nav-list">
        <ListGroup >
           {
             this.props.list.map((obj,ind)=>{
               return (
                 <ListGroupItem key={ind} header={obj.name} className='hover' {...{[(ind===this.state.focus)?'active':null]:'true'}} onClick={()=>{this.setFocus(ind)}}>
                     {obj.des}
                 </ListGroupItem>
               )
             })
           }
        </ListGroup>
      </div>
    );
  }
}

export default NavList;



